.headerWrapper {
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.header {
  width: 100%;
  position: absolute;
  top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-around;

  .logo {
    display: flex;
    flex-direction: column;
    gap: 8px;
    height: 80px;
    width: 80px;

    background-image: url('../../assets/logo/LogoW.png');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .logoB {
    display: flex;
    flex-direction: column;
    gap: 8px;
    height: 80px;
    width: 80px;

    background-image: url('../../assets/logo/LogoB.png');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.logo span {
  font-size: 22px;
  color: rgb(255, 255, 255);
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  display: flex;
  flex-direction: column;
}
.logoB span {
  font-size: 22px;
  color: rgb(0, 0, 0);
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  display: flex;
  flex-direction: column;
}

.navigation {
  width: 60%;
  height: 100px;
  display: flex;
  justify-content: space-between;

  transform: translateY(20px);
  opacity: 0;
  animation: fadeInUp2 0.5s ease-in-out forwards;
}

.navigationSmall{
  width: 20%;
  height: 100px;
  display: none;
  /* display: flex;
  justify-content: center; */
}

@keyframes fadeInUp2 {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}


@media (max-width: 1150px) {
  .logo span {
    font-size: 18px;
  }
  .logoB span {
    font-size: 18px;
  }
  .navigation{
    display: none;
  }
  .navigationSmall{
    display: flex;
    align-items: center;
    justify-content: end;
  }
}


@media (max-width: 700px) {
  .header {
    width: 100%;
    position: absolute;
    top: 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;
  
    .logo {
      display: flex;
      flex-direction: column;
      gap: 8px;
      height: 60px;
      width: 60px;
  
      background-image: url('../../assets/logo/LogoW.png');
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  
    .logoB {
      display: flex;
      flex-direction: column;
      gap: 8px;
      height: 60px;
      width: 60px;
  
      background-image: url('../../assets/logo/LogoB.png');
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
}
