@media (max-width: 1040px) {
    .slideWrapper{
        width: 200px;
        height: 400px;
    }
    .swiperItem .textBlock{
        width: 30%;
        height: 40%;
    }
}

@media (max-width: 800px){
    .swiperItem .textBlock{
        width: 35%;
        height: 40%;
    }
}

@media (max-width: 650px){
    .swiperItem .textBlock{
        width: 40%;
        height: 40%;
    }
}

@media (max-width: 580px){
    .swiperItem .textBlock{
        width: 50%;
        height: 40%;
    }
}

@media (max-width: 470px){
    .swiperItem .textBlock{
        width: 60%;
        height: 40%;
    }
}